.about {
    margin: 0 auto;
    background: #252934;
    align-items: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    overflow: hidden;

    .header {
        width: 100vw;
        position: absolute;
        text-align: center;
        overflow: hidden;
        top: 105vh;
        display: flex;
        align-items: center;
        flex-direction: column;
        justify-content: center;

        h2 {
            font-size: 3.5rem;
            color: white;

        }

        .underline {
            margin-top: 25px;
            background: white;
            height: 4px;
            width: 80px;
            overflow: hidden;
            // width: 100%;
            // top: 210vh;
        }

    }

    .programs-title {
        margin-bottom: 2rem;
        font-weight: 700;
        color: white;
        font-size: 2rem;
        overflow: hidden;
    }

    .programs {
        align-items: center;
        display: flex;
        flex-direction: row;
        justify-content: center;
        overflow: hidden;
    
        &.mobile {
            flex-direction: column;
            justify-content: center;
            align-items: center;
        }
    
        .program {
            // height: 228px;
            width: 200px;
            padding: 1rem 1rem;
            overflow: hidden;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
        
            .image {
                left: 15%;
                width: 70%;
                height: 100%;
                background-size: cover;
                background-position: center;
                background-repeat: no-repeat;
                overflow: hidden;
                margin-bottom: 0.7rem;
            }

            .title {
                position: relative;
                text-align: center;
                color: white;
                font-weight: 600;
                line-height: 1.5rem;
                font-size: 1.5rem;
                overflow: hidden;
                letter-spacing: 1px;
                margin-bottom: 6px;
                padding: 4px 0;
            }

            .examples {
                position: relative;
                text-align: center;
                color: rgb(0,170,255);
                overflow: hidden;
                padding: 3px 0;
                outline: white;
                outline-width: 2px;
                outline-style: solid;
                padding: 0.2rem 0.5rem;
                text-decoration: none;

                -webkit-touch-callout: none;
                -webkit-user-select: none;
                -khtml-user-select: none;
                -moz-user-select: none;
                -ms-user-select: none;
                user-select: none;

                transition: all 0.2s;
            
                &:hover {
                    outline-color: rgb(0,170,255);
                    background-color: rgb(0,170,255);
                    color: white;
                }

                &:active {
                    outline-color: rgb(0, 120, 180);
                    background-color: rgb(0,120,180);
                    color: lightgrey;
                }

            }
        }
    }
}