.projects {
    background: #252a36;
    margin: 0 auto;
    align-items: center;
    // flex-direction: column;
    // justify-content: center;
    overflow: hidden;

    .header {
        width: 100vw;
        position: absolute;
        text-align: center;
        overflow: hidden;
        top: 205vh;
        display: flex;
        align-items: center;
        flex-direction: column;
        justify-content: center;

        h2 {
            font-size: 3.5rem;
            color: white;

        }

        .underline {
            margin-top: 25px;
            background: white;
            height: 4px;
            width: 80px;
            overflow: hidden;
            // width: 100%;
            // top: 210vh;
        }

    }

    .types {
        margin-top: 17vh;
        align-items: center;
        display: flex;
        flex-direction: row;
        justify-content: center;
        overflow: hidden;

        .type {
            background-color: transparent;
            padding: 1rem 2rem;
            overflow: hidden;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;

            transition: all 0.3s;
            -webkit-touch-callout: none;
            -webkit-user-select: none;
            -khtml-user-select: none;
            -moz-user-select: none;
            -ms-user-select: none;
            user-select: none;

            p {
                color: white;
                font-size: 1.2rem;
                font-weight: 600;
            }

            &.active {
                background-color: rgb(0,170,255);
            }

            &:hover {
                outline: 3px rgb(0,170,255) solid;
                outline-offset: -3px;
            }

        }

    }

    .container {
        margin: 2rem 3rem;
        align-items: center;
        display: flex;
        flex-direction: row;
        justify-content: center;
        overflow: hidden;
        flex-wrap: wrap;


        .project {
            width: 33.33%;
            aspect-ratio: calc(16/9);
            overflow: hidden;
            position: relative;

            &.inactive {
                visibility: hidden;
                position: absolute;
                display: none;
            }

            &:nth-child(1) { // This website
                background: url("../Images/Backgrounds/My Website.png") center center/cover;
            }

            &:nth-child(2) { // Haze Studios
                background: url("../Images/Backgrounds/Haze Studios.png") center center/cover;
            }

            .content {
                height: 100%;
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;
                top: 0;
                transition: background-color 0.5s;
    
                .title {
                    position: relative;
                    color: transparent;
                    font-weight: 600;
                    line-height: 1.5rem;
                    font-size: 1.5rem;
                    overflow: hidden;
                    letter-spacing: 1px;
                    margin-bottom: 6px;
                    margin-top: 3rem;
                    padding: 4px 0;
                    
                    transition: background-color 0.5s;
                }

                .made {
                    position: relative;
                    color: transparent;
                    font-weight: 600;
                    line-height: 1rem;
                    font-size: 1rem;
                    overflow: hidden;
                    letter-spacing: 1px;
                    
                    transition: background-color 0.5s;

                    span {

                        color:transparent;

                    }

                }

                .select {
                    visibility: hidden;
                    background-color: transparent;
                    overflow: hidden;
                    display: flex;
                    flex-direction: column;
                    justify-content: center;
                    align-items: center;
                    margin-top: 3rem;
                    margin-bottom: 1rem;
        
                    -webkit-touch-callout: none;
                    -webkit-user-select: none;
                    -khtml-user-select: none;
                    -moz-user-select: none;
                    -ms-user-select: none;
                    user-select: none;
        
                    a {
                        padding: 1rem 3.5rem;
                        color: #252a36;
                        font-size: 1.2rem;
                        font-weight: 600;
                        outline: 3px rgb(0,170,255) solid;
                        outline-offset: -3px;
                        text-decoration: none;
                        text-align: center;
                        transition: background-color 0.5s;
                        overflow: hidden;
                        height: 0.8rem;
                        line-height: 0.8rem;

                        &:hover {
                            background-color: rgb(0,170,255);
                        }
    
                        &:active {
                            background-color: rgb(0, 120, 180);
                            outline-color: rgb(0,120,180);
                        }
                    }

                }

            }
    
            &:hover .content {
                background: rgba(255, 255, 255, 0.8);

                .title {
                    color: #252a36;
                }

                .made {
                    color: #252a36;

                    span {
                        color:rgb(0,170,255);
                    }
                }

                .select {
                    visibility: visible;
                }
            }
        }
    }
}