.home {
    background-color: #252934;
    align-items: center;
    display: flex;
    justify-content: center;
    flex-direction: column;

    #tsparticles {
        overflow: hidden;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 1;

        Particle {
            width: 100%;
            height: 100%;
        }
    }

    .center {

        background: transparent;
        z-index: 2;
        align-items: center;
        display: flex;
        justify-content: center;
        flex-direction: column;

        .text {
            font-size: 32pt;
            line-height: 36pt;
            font-weight: 450;

            overflow: hidden;
            color: white;

            -webkit-touch-callout: none;
            -webkit-user-select: none;
            -khtml-user-select: none;
            -moz-user-select: none;
            -ms-user-select: none;
            user-select: none;

            &.mobile {
                font-size: 16pt;
                line-height: 20pt;
                font-weight: 400;
            }

            span {
                font-weight: 500;
                color: rgb(0,170,255);
            }
        }

        .job {
            font-size: 20pt;
            line-height: 25pt;
            font-weight: 450;

            overflow: hidden;
            color: white;

            -webkit-touch-callout: none;
            -webkit-user-select: none;
            -khtml-user-select: none;
            -moz-user-select: none;
            -ms-user-select: none;
            user-select: none;

            &.mobile {
                font-size: 15pt;
                line-height: 18pt;
                font-weight: 400;
            }

            &.desktop {
                font-size: 30pt;
                line-height: 36pt;
                font-weight: 500;
            }

            span {
                font-weight: 500;
                color: rgb(0,170,255);
            }
        }
    }

}