.navbar {
  position: fixed;
  background: transparent;
  color: white;
  transition: all 0.3s ease-out;
  z-index: 100;
  width: 100%;
  height: auto;

  // 
  .menu {
    margin-top: 1rem;
    height: 2rem;
    width: auto;
    overflow: hidden;
    
    .content {
      width: 0%;
      
      -webkit-transition: all 1s ease-in-out;
      -moz-transition: all 1s ease-in-out;
      -o-transition: all 1s ease-in-out;
      transition: all 1s ease-in-out;

      ul {

        li {

          float: right;
          padding: 0.5rem 0;
          padding-right: 1.5rem;

          a {
            color: lightgrey;
            text-decoration: none;
            font-size: 1.2rem;
            font-weight: bold;
            transition: all 0.3s ease-out;

            -webkit-touch-callout: none;
            -webkit-user-select: none;
            -khtml-user-select: none;
            -moz-user-select: none;
            -ms-user-select: none;
            user-select: none;

            &:hover {
              color: grey;
            }
          }
        }
      }

      &.active {
        width: auto;
      }
    }
  }


  // Icon
  .navbar-icon {
    //float the icon on the right of the screen
    float: right;
    margin-right: 1rem;
    margin-top: 1rem;
    cursor: pointer;

    width: 2.5rem;
    height: 2.5rem;
    overflow: hidden;
    background-color: transparent;

    .icon {
      width: 100%;
      height: 100%;
    }

    &.mobile {
      width: 2rem;
      height: 2rem;
    }
  }

}